#navbar {
    background-color: maroon;
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    padding-right: 200px;
  
  }
  
  
  ul {
    margin: 0;
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
    justify-content: space-around;
    align-items: center;
  }
  
  li:hover {
    cursor: pointer;
    background-color: rgb(194, 10, 10);
  }

  li {
    padding: 12px;
    font-size: 1.8rem;
    grid-column: span 1;
    display: inline-block;
  }

  .navlink {
    color: rgb(219, 218, 218)
  }

  span {
    transition: font-size 0.5s ease-in-out;  
  }

  .bignumb {
    font-size: 2.3rem; 
  }

  #active {
    color: white;
  }

  .homelink {
    color: rgb(248, 122, 143);
    font-weight: 900;   
    font-size: 2rem;
  }

  #activehome {
    color: rgb(252, 177, 190);
  }